import React, {useMemo, useState, useRef, useEffect} from 'react'
import DateSelector from './DateSelector'
import { useSelectedDate } from '../context/DateContext'
import { fetchMachines, fetchLogs, getLogsForDate, getEventStatus, getLogStatus } from '../services/api'
import Timeline from 'react-calendar-timeline'
import moment from 'moment';
// react-calendar-timeline ships its CSS under lib/Timeline.css
import 'react-calendar-timeline/lib/Timeline.css';


interface Log {
  id: number;
  machine_name: string;
  event: string;
  total_count?: number;
  interval_count?: number;
  machine_rate?: number;
  comments?: string;
  mo?: string;
  part_number?: string;
  operator_id?: string;
  shift_number?: string;
  created_at: string;
}

export default function TimelineView({ onMachineSelect }: { onMachineSelect?: () => void }){
  const { selectedDate } = useSelectedDate()
  const dateStr = selectedDate.toISOString().split('T')[0]
  const [machinesData, setMachinesData] = useState<any[]>([])
  const [logsData, setLogsData] = useState<any[]>([])
  const [logs, setLogs] = useState<any[]>([])
  const [loading, setLoading] = useState(true)

  // Fetch machines and logs from database
  useEffect(() => {
    const loadData = async () => {
      setLoading(true)
      try {
        const [machines, logs] = await Promise.all([
          fetchMachines(),
          fetchLogs()
        ])
        setMachinesData(machines)
        setLogsData(logs)
      } catch (error) {
        console.error('Failed to fetch data:', error)
      } finally {
        setLoading(false)
      }
    }
    loadData()

  }, [])

  const [nowTs, setNowTs] = useState<number>(() => moment().valueOf());
  useEffect(() => {
    const iv = setInterval(() => setNowTs(moment().valueOf()), 10000); // update every 10s
    return () => clearInterval(iv);
  }, []);

  console.log("nowTs*****", nowTs)

  // Tooltip state for item hover info
  const [clickedItem, setClickedItem] = useState<any>(null);
  const [tooltipPos, setTooltipPos] = useState<{ x: number; y: number } | null>(null);
  const mouseDownPosRef = useRef<{ x: number; y: number } | null>(null);

  useEffect(() => {
    const dateLogs = logsData.filter(log => log.created_at.startsWith(dateStr))
    console.log("dateLogs:", dateLogs.length, dateLogs)
    setLogs(dateLogs)
  }, [logsData, dateStr])

  const eventColor = (event?: string) => {
    if (!event) return '#303030';
    console.log("event_logs", event)
    const e = event.trim().toLowerCase();
    const map: Record<string, string> = {
      'start button': '#E01C21',
      'auto interval log': '#0aac00',
      'off': '#111827',
    };
    if (map[e]) return map[e];
    return '#303030';
  };

  const itemRenderer = (props: any) => {
    const { item, timelineContext, itemContext, getItemProps, getResizeProps } = props;
    const { left: leftResizeProps, right: rightResizeProps } = getResizeProps();
    const baseColor = item.bgColor || '#303030';

    // Get props from the lib, but DON'T pass style here yet
    const rawItemProps = getItemProps({
      onMouseDown: (e: any) => {
        mouseDownPosRef.current = { x: e.clientX, y: e.clientY };
        setClickedItem(null);
        setTooltipPos(null);
      },
      onMouseUp: (e: any) => {
        const down = mouseDownPosRef.current;
        if (down) {
          const dx = e.clientX - down.x;
          const dy = e.clientY - down.y;
          const distSq = dx * dx + dy * dy;
          // show tooltip only when mouse moved less than or equal to 10px
          if (distSq <= 10 * 10 && (item.comment == "NORMAL SPEED" || item.comment == "SPEED LOSS")) {
            setClickedItem(item);
            setTooltipPos({ x: e.clientX, y: e.clientY });
          }
          mouseDownPosRef.current = null;
        }
      },
    });

    // Pull out key & style from what the lib gives us
    const { key, style: rawStyle = {}, ...restItemProps } = rawItemProps as {
      key: React.Key;
      style: React.CSSProperties;
    };

    // Remove any `background` shorthand, unify on `backgroundColor`
    const {
      background,
      backgroundColor: libBackgroundColor,
      ...restStyle
    } = rawStyle;

    const finalBgColor = baseColor || libBackgroundColor || (background as string) || 'transparent';

    const finalStyle: React.CSSProperties = {
      ...restStyle,                  // position, width, top, etc. from the lib
      backgroundColor: finalBgColor, // only longhand
      color: item.color,
      borderColor: finalBgColor,
      borderStyle: 'solid',
      borderWidth: 1,
    };

    return (
      <div key={key} {...restItemProps} style={finalStyle}>
        {itemContext.useResizeHandle ? <div {...leftResizeProps} /> : null}

        <div
          style={{
            height: itemContext.dimensions.height,
            overflow: 'hidden',
            paddingLeft: 3,
            textOverflow: 'ellipsis',
            whiteSpace: 'nowrap',
          }}
        >
          {itemContext.title}
        </div>

        {itemContext.useResizeHandle ? <div {...rightResizeProps} /> : null}
      </div>
    );
  };

    const items = useMemo(() => {
    const result: any[] = [];

    // Build name -> id map for assigning groups
    const nameToId: Record<string, number> = {};
    machinesData.forEach((m) => {
      nameToId[m.name] = m.id;
    });

    // First log timestamp per machine (logs assumed ascending by created_at)
    const firstLogByMachine: Record<string, string> = {};
    // Last log timestamp per machine
    const lastLogByMachine: Record<string, string> = {};
    const lastLogObjByMachine: Record<string, Log> = {};
    for (let i = 0; i < logs.length; i++) {
      const l = logs[i];
      // maintain the earliest (first) log timestamp per machine
      const prevFirst = firstLogByMachine[l.machine_name];
      if (!prevFirst || moment(l.created_at).isBefore(moment(prevFirst))) {
        firstLogByMachine[l.machine_name] = l.created_at;
      }
      // maintain the latest (last) log timestamp per machine and keep last log object
      const prevLast = lastLogByMachine[l.machine_name];
      if (!prevLast || moment(l.created_at).isAfter(moment(prevLast))) {
        lastLogByMachine[l.machine_name] = l.created_at;
        lastLogObjByMachine[l.machine_name] = l;
      }
    }

    // Persist last-known standardPartsRate per machine (from Start Shift logs)
    const lastRateByMachine: Record<string, number> = {};

    // Iterate sequentially so Start Shift rates apply to later logs
    for (let index = 0; index < logs.length - 1; index++) {
      const log = logs[index];
      const nextLog = logs[index + 1];
      const start = moment(log.created_at);
      const end = moment(nextLog.created_at);

      // Example comments: "Standard Parts Rate: 5000 parts/hr, Planned Quantity: 1000 parts"
      // Extract Standard Parts Rate number if present on this log
      let parsedRate: number | undefined = undefined;
      if (typeof log.comments === 'string' && log.event.trim().toLowerCase() === 'start shift') {
        const rateMatch = /Standard Parts Rate:\s*([\d,]+)\s*parts/i.exec(log.comments);
        if (rateMatch && rateMatch[1]) {
          parsedRate = Number(rateMatch[1].replace(/,/g, ''));
        }
      }

      // If this log provided a rate (typically 'Start Shift'), persist it for the machine
      if (typeof parsedRate === 'number' && log.machine_name) {
        lastRateByMachine[log.machine_name] = parsedRate;
      }

      // Determine the effective standardPartsRate to use: parsed for this log or last-known for that machine
      const standardPartsRate =
        typeof parsedRate === 'number'
          ? parsedRate
          : log.machine_name && typeof lastRateByMachine[log.machine_name] === 'number'
          ? lastRateByMachine[log.machine_name]
          : undefined;

      // Determine background color; default via eventColor
      let bg = eventColor(log.event);
      let comment = "";

      // If this is an Auto interval log, compare the effective standard rate
      // with the reported machine_rate and color accordingly.
      if (typeof log.event === 'string' && log.event.trim().toLowerCase() === 'auto interval log') {
        if (typeof standardPartsRate === 'number' && typeof log.machine_rate === 'number') {
          const a = standardPartsRate;
          const b = log.machine_rate;
          const percent = b / a;
          if (percent >= 0.7) {
            bg = "#0AAC00";
            comment = "NORMAL SPEED";
          }
          else if (percent >= 0.4) {
            bg = "#FCF402";
            comment = "SPEED LOSS";
          }
          else {
            bg = "#E01C21";
            comment = "SPEED LOSS";
          }
        }
      }

      const item = {
        id: index + 1,
        // group is derived from machine name -> machine id mapping
        group: typeof nameToId[log.machine_name] === 'number' ? nameToId[log.machine_name] : 0,
        canMove: false,
        canResize: false,
        title: log.event.trim().toLowerCase() === 'auto interval log' ? "" : log.event,
        start_time: start,
        end_time: end,
        color: 'white',
        bgColor: bg,
        comment: comment,
        product: log.part_number || '',
        start: moment(log.created_at, "YYYY-MM-DD HH:mm:ss").format("hh:mmA")
      };

      result.push(item);
    }

    // Add 'Off' items per machine from 00:00 to first log (or full day if no logs)
    const dayStart = dateStr ? moment(dateStr).startOf('day') : moment().startOf('day');
    const dayEnd = dayStart.clone().add(1, 'day');

    for (const m of machinesData) {
      const firstTs = firstLogByMachine[m.name];
      if (firstTs) {
        const firstMoment = moment(firstTs);
        if (firstMoment.isAfter(dayStart)) {
          result.push({
            id: `off-${m.id}`,
            group: m.id,
            canMove: false,
            canResize: false,
            title: 'Off',
            start_time: dayStart,
            end_time: firstMoment,
            color: 'white',
            bgColor: '#111827',
          });
        }
        // Also add Off from last log to now (clamped to day end) only when
        // the last log's event is 'Off' OR there is no recent health-check
        const lastTs = lastLogByMachine[m.name];
        const lastLogObj = lastLogObjByMachine[m.name];
        const hasHealth = (m as any).last_updated;
        const stepMs = 10 * 1000;
        const quantizedNow = moment(Math.floor(moment(nowTs).valueOf() / stepMs) * stepMs);
        const nowClamped = moment.min(quantizedNow, dayEnd);
        if (lastTs) {
          const lastMoment = moment(lastTs).clone();
          const lastEventIsOff = lastLogObj && typeof lastLogObj.event === 'string' && lastLogObj.event.trim().toLowerCase() === 'off';
          const noHealth = !hasHealth;
          if ((lastEventIsOff || noHealth) && lastMoment.isBefore(nowClamped)) {
            result.push({
              id: `off-after-${m.id}`,
              group: m.id,
              canMove: false,
              canResize: false,
              title: 'Off',
              start_time: lastMoment,
              end_time: nowClamped,
              color: 'white',
              bgColor: '#111827',
            });
          }
        }
      } else {
        // no logs for this machine  mark Off from day start up to now (clamped to day end)
        const stepMs = 10 * 1000;
        const quantizedNow = moment(Math.floor(moment(nowTs).valueOf() / stepMs) * stepMs);
        const nowClamped = moment.min(quantizedNow, dayEnd);
        result.push({
          id: `off-${m.id}`,
          group: m.id,
          canMove: false,
          canResize: false,
          title: 'Off',
          start_time: dayStart,
          end_time: nowClamped,
          color: 'white',
          bgColor: '#111827',
        });
      }
    }

    return result;
  }, [logs, machinesData, dateStr, nowTs]);

    if (!dateStr) {
    return (
      <div className="h-64 flex items-center justify-center bg-white dark:bg-stone-800 rounded-b-lg shadow-sm border-t border-stone-100 dark:border-stone-700">
        <div className="text-stone-500 dark:text-stone-400">Select a date to view histogram.</div>
      </div>
    );
  }

  if (loading) {
    return (
      <div className="h-64 flex items-center justify-center bg-white dark:bg-stone-800 rounded-b-lg shadow-sm border-t border-stone-100 dark:border-stone-700">
        <div className="text-stone-500 dark:text-stone-400">Loading histogram for {dateStr}...</div>
      </div>
    );
  }

  // if (error) {
  //   return (
  //     <div className="h-64 flex items-center justify-center bg-white dark:bg-stone-800 rounded-b-lg shadow-sm border-t border-stone-100 dark:border-stone-700">
  //       <div className="text-red-500">{error}</div>
  //     </div>
  //   );
  // }

  const groups = machinesData.map((m) => {
    // Calculate total performing hours (all auto interval logs combined) as percent of selected day
    let totalPerformingMs = 0;
    
    for (let i = 0; i < logs.length - 1; i++) {
      const log = logs[i];
      const nextLog = logs[i + 1];
      if (log.machine_name === m.name && typeof log.event === 'string' && log.event.trim().toLowerCase() === 'auto interval log' &&
          nextLog.machine_name === m.name && typeof nextLog.event === 'string' && nextLog.event.trim().toLowerCase() === 'auto interval log') {

        const startMoment = moment(log.created_at);
        const endMoment = moment(nextLog.created_at);
        const durationMs = endMoment.valueOf() - startMoment.valueOf();
        if (durationMs > 0) {
          totalPerformingMs += durationMs;
        }
      }
    }
    
    // Calculate percent of full day (24 hours = 86400000 ms)
    const dayMs = 24 * 60 * 60 * 1000;
    const performingPercent = Math.round((totalPerformingMs / dayMs) * 100);
    const rightTitleText = `${performingPercent}%`;
    
    return { id: m.id, title: m.name, rightTitle: rightTitleText, stackItems: false, height: 60 };
  });
  
  const defaultTimeStart = dateStr ? moment(dateStr).startOf('day') : moment().startOf('day');
  const defaultTimeEnd = defaultTimeStart.clone().add(1, "day");

  console.log("machinesData_logs", machinesData)
  console.log("dateStr", dateStr)
  console.log("dateLogs", logs)
  console.log("nowTs", nowTs)

  return (
    <div>
      <DateSelector />
      <h2>Factory (Timeline) View - {selectedDate.toLocaleDateString('en-US', { year: 'numeric', month: 'short', day: 'numeric' })}</h2>
      <Timeline
        groups={groups}
        items={items}
        defaultTimeStart={defaultTimeStart.valueOf()}
        defaultTimeEnd={defaultTimeEnd.valueOf()}
        itemHeightRatio={1.5}
        canMove={false}
        canResize={false}
        itemRenderer={itemRenderer}
        minZoom={1000 * 10} // 10 minutes
        // sidebarWidth={100}
        rightSidebarWidth={60}
      />
      {clickedItem && tooltipPos && (
        <div
          style={{
            position: 'fixed',
            left: `${tooltipPos.x - 60}px`,
            top: `${tooltipPos.y + 15}px`,
            backgroundColor: '#000000',
            color: '#ffffff',
            padding: '8px 12px',
            borderRadius: '6px',
            fontSize: '12px',
            zIndex: 1000,
            boxShadow: '0 2px 8px rgba(0, 0, 0, 0.3)',
            maxWidth: '300px',
            pointerEvents: 'none',
            border: '1px solid #374151',
          }}
        >
          <div style={{ display: 'flex', alignItems: 'center', gap: 8, marginBottom: 6 }}>
            <div style={{ width: 12, height: 12, borderRadius: 6, backgroundColor: clickedItem.bgColor || '#ccc', border: '1px solid rgba(255,255,255,0.12)' }} />
            <div style={{ fontWeight: 600 }}>{clickedItem.comment || ''}</div>
          </div>
          <div><strong>PRODUCT:</strong> {clickedItem.product || ''}</div>
          <div><strong>START:</strong> {clickedItem.start || (clickedItem.start_time ? clickedItem.start_time.format('hh:mmA') : '')}</div>
        </div>
      )}
    </div>
  )
}
